import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export default class HeaderService {
    private setGnb = new Set<{[key: string]: string}>();
    private setUtil = new Set<{[key: string]: string}>();
    private setResume: {[key: string]: string};

    get getGnbMenus(): Set<{[key: string]: string}> {
        return this.setGnb
            .add({name: 'features', url: '#features', title: ''})
            .add({name: 'licenses', url: '#licenses', title: ''})
            .add({name: 'technique', url: '#technique', title: ''})
            .add({name: 'workbench', url: '#workbench', title: ''})
            .add({name: 'portfolio', url: '#portfolio', title: ''})
            .add({name: 'development', url: '#development', title: 'UI'})
            .add({name: 'wiki', url: '#wiki', title: ''})
            .add({name: 'blog', url: '#blog', title: ''});
    }

    get getUtilMenus(): Set<{[key: string]: string}> {
        return this.setUtil
            .add({name: 'introduction', url: 'assets/resume/introduction.html', title: ''})
            .add({name: 'personality', url: 'assets/resume/personality_type.html', title: ''})
            .add({name: 'github', url: 'https://github.com/woonyzzang?tab=repositories', title: ' '})
            .add({name: 'blog', url: 'https://woonyzzang.github.io/', title: ''});
    }

    get getResume(): {[key: string]: string} {
        return this.setResume = {name: 'resume', url: 'assets/resume/resume.html', title: ''};
    }
}
